import React from 'react';
import './Story.css';
import { withRouter } from "react-router";
import './my-button.css'

class Story extends React.Component{
    constructor (props) {
      super(props)
      this.state = {
        isAgree: false,
        isTimerEnd: false,
        seconds: 50,
      }
    }

    handleClick = () => {
      if (this.props.isRegular === true) {
        if (this.props.isShort === true) {
          this.props.history.push({pathname: '/r/s/wait_room'});
        }
        else {
          this.props.history.push({pathname: '/r/l/wait_room'});
        }
      }
      else {
        if (this.props.isShort === true) {
          this.props.history.push({pathname: '/v/s/wait_room'});
        }
        else {
          this.props.history.push({pathname: '/v/l/wait_room'});
        }
      }
    }

    componentDidMount()
    {
      setInterval(() => {
        if (this.state.seconds !== 0) {
          this.setState({seconds: this.state.seconds-1})
        }
        else {
          if (this.state.isTimerEnd === false)
          {
            this.setState({isTimerEnd: true});
          }
        }
      }, 1000)
    }

    render(){
      var isRegular = this.props.isRegular;
      return (
       <div className="story-flex-content">
        <div className="story-flex-content-text">
            {isRegular === true ? 
              <h3 style={{textAlign: "left"}}>
                You and your partner recently returned from your honeymoon (that was pretty nice by the way) and you are going to move into your new apartment.<br></br>
                So firstly, you have decided to purchase some basic items for your new kitchen such as a fridge, worktop surface and stove. 
                With the advice of your friends, Ross and Rachel, you decided to buy at Shlomo’s Kitchens, though you have never been in this store.
                You have a budget of $5300 for all 3 items together.
                At each step you will choose a product you like the most that would nicely fit your new kitchen and budget as well. At each step you can view additional attractive options by clicking on the “View more”  button for a 3 second of waiting.
                You want to make all the items look perfectly together in your new dream kitchen.<br></br>
                So, let’s start!
              </h3> 
              : 
              <h3 style={{textAlign: "left"}}>
                You are an interior designer who specializes in kitchen styling. <br></br>
                One of your favorite furniture suppliers is Shlomo’s Kitchens store. 
                You get along with its owner, Shlomo. 
                He usually makes special prices for you, so you are a regular customer at his store.
                You’ve got an order from your new customer, so you are going to design the kitchen interior in his house. Firstly you need to purchase 3 items at Shlomo’s store. The items are a fridge, worktop surface and stove.
                You have a budget of $5300 for all 3 items together.
                At each step you will choose a product you like the most that would nicely fit the customer’s new kitchen and the budget as well. At each step you can view additional attractive options by clicking on the “View more” button for a 3 second of waiting.
                You are going to amaze your new customer because you want him contacting you the next time.<br></br>
                So, let’s start!
              </h3> 
              }
            <br></br>
            
          </div>
          <button disabled={!this.state.isTimerEnd} className="my-button" onClick={()=> this.handleClick()}>Lets start in {this.state.seconds}</button>
       </div>
      );
    }
  }
  
  export default withRouter(Story);