class TechSurvey extends React.Component{
    constructor (props) {
      super(props)
      this.state = {
        isRegular: true,
      }
      this.sendReportToEmail = this.sendReportToEmail.bind(this);
    }

    onComplete(survey, options) {
      //Write survey results into database
      var surveyResults = JSON.stringify(survey.data);
      console.log("Survey results: " + JSON.stringify(survey.data));
      this.sendReportToEmail(surveyResults);
     }

     sendReportToEmail(surveyResultsInput) {
        var templateParams = {
            finishDate: this.props.location.serviceResults.finishDate,
            totalPrice: this.props.location.serviceResults.totalPrice,
            budget: this.props.location.serviceResults.budget,
            from_name: 'Experiment',
            isRegular: this.props.location.serviceResults.isRegular,
            clickedRegularViewMore: this.props.location.serviceResults.clickedRegularViewMore,
            clickedVipViewMore: this.props.location.serviceResults.clickedVipViewMore,
            totalTime: this.props.location.serviceResults.totalTime,
            waitingRoomTime: this.props.location.serviceResults.waitingRoomTime,
            surveyResults: surveyResultsInput,
        };
        emailjs.send('service_8ww919p', 'template_h1x55nb', templateParams, 'user_KnDPo0mi8tW6gzapNbVeb')
            .then(function(response) {
               console.log('SUCCESS!', response.status, response.text);
            }, function(error) {
               console.log('FAILED...', error);
               alert("Something went wrong... Please try again in a few minutes.");
            });
    }


    render() {
        var surveyRender = <Survey.Survey json={json} onComplete={(survey, options) => this.onComplete(survey, options)}/>;
        return (
            <div className="survey-content">
                <div>
                  {surveyRender}
                </div>
            </div>
        )
    }
}

export default withRouter(TechSurvey);

